import { RendererCanvas } from '../../../lib'

export class Solution {
    public divHost: HTMLElement;
    public renderer: Renderer;
    constructor(id: string) {
        this.divHost = document.getElementById(id);

        // Center align
        this.divHost.style.display = 'flex';
        this.divHost.style.alignItems = 'center';

        const div = document.createElement('div');
        div.style.marginLeft = 'auto';
        div.style.marginRight = 'auto';
        div.style.backgroundColor = '#555';
        div.style.width = '1000px';
        div.style.height = '600px';

        this.divHost.appendChild(div);

        // TODO
        // You code goes here for one time operation

        this.renderer = new Renderer(div);

        // console.log(this.renderer);
        // !! you should execute it for rendering
        // this.start();
    }
    public destroy() {
        this.renderer.destroy();
        while (this.divHost.lastElementChild) {
            this.divHost.removeChild(this.divHost.lastElementChild);
        }
    }
}
// https://codesandbox.io/s/framer-motion-animate-on-state-update-ns67ib?from-embed
export class Renderer extends RendererCanvas {
    listPoint = [];
    rect;
    t = 0.0;
    N = 200;
    constructor(div: any) {
        super(div);
        this.canvas.style.position = 'relative';
        this.canvas.style.width = '100%';
        this.canvas.style.height = '100%';

        // TODO
        // You code goes here for one time operation

        const button = document.createElement('button');
        button.innerHTML = "reset timeer";
        button.addEventListener('click', () => {
            this.t = 0.0;
            this.rect.updateTargetPos(Math.random() * this.canvas.width, Math.random() * this.canvas.height)
        });
        div.append(button);

        const slider = getSlider('N', 0, 1000);
        this.divHost.appendChild(slider.div);
        slider.slider.oninput = () => {
            this.N = +slider.slider.value
            // console.log(slider.slider.value)
            slider.text.innerText = 'Deg: ' + this.N;
            this.rect.updateTargetRotation(+this.N)
        }

        // 
        // https://en.wikipedia.org/wiki/Interpolation
        this.listPoint.push(new Point(50, 50, linear));
        this.listPoint.push(new Point(100, 50, easeInElastic));
        this.listPoint.push(new Point(150, 50, easeOutElastic2));

        this.rect = new Rect(500, 500, 80, 80);

        this.start();
    }
    public mouseDown(x: number, y: number): void {
        this.rect.updateTargetPos(x,y);
    }
    public render(ctx: CanvasRenderingContext2D): void {
        // TODO
        // You code goes here for the rending loop
        for(let i = 0 ; i < this.listPoint.length; ++i) {
            this.listPoint[i].update(this.t);
           
            // safegurard for the x value of the Points
            // if (this.listPoint[i].x > 120) {
            //  this.listPoint[i].x = 120; 
            // }
           
           this.listPoint[i].render(ctx);
           this.listPoint[i].y = 100 + i * 100;
           
       
         }

         this.rect.update(this.t);
         this.rect.render(ctx);

         // console.log(easeOutElastic2(t));
         this.t += 0.005;

        // !! can stop render after this frame.
        // this.isStatic = true;
    }

}

