class Point {
  public x: number = 0;
  public y: number = 0;
  public r: number = 5;
  interpolation: any;
  constructor(x: number, y: number, interpolation: any ){
    this.x = x;
    this.y = y;
    this.interpolation = interpolation;
  }
  public render(ctx: CanvasRenderingContext2D) {
    ctx.beginPath();
    ctx.arc(this.x, this.y, this.r, 0, 3.14 * 2)
    ctx.closePath()
    ctx.fill()
  }
  public update(t: number){
    let v = this.interpolation(t);
    // console.log(v)

    if(this.interpolation === linear && v > 1.0 ) {
        return;
    }

    this.x = v * 200;
  }
  
}

class Rect {
    // x: number;
    // y: number;
    width: number;
    height: number;
    vec = [0, 0]

    tp = [0, 0]
    op = [0, 0]

    t = 0;

    rotation = 200
    or = 0
    tr = 0

    tRotation = 0
    constructor(x, y, width, height) {
        this.vec = [x, y]
        this.tp = [x, y];
        this.op = [x, y];

        this.width = width;
        this.height = height;
    }
    updateTargetPos(x: number, y: number) {
        this.op[0] = this.vec[0];
        this.op[1] = this.vec[1];

        this.tp[0] = x;
        this.tp[1] = y;

        // console.log(x, y)
        this.t = 0;
    }
    updateTargetRotation(deg: number) {
        this.rotation = deg
        this.tr = deg;

        this.tRotation = 0;
    }
    update() {
        // if (this.tp[0] !== this.op[0] || this.tp[1] !== this.op[1]) {
            // let t = easeOutElastic2(this.t)
            // console.log(t)
            let v = interpolationElastic(this.vec, this.op, this.tp, this.t);
            this.vec[0] = v[0]
            this.vec[1] = v[1]
            // this.op[0] = v[0];
            // this.op[1] = v[1];
            //   this.x = v[0];
            //   this.y = v[1];
            this.t+= 0.005
        // } 


        this.rotation = interpolationElasticValue(this.rotation, this.or, this.tr, this.t);
        this.tRotation += 0.0001;

    }
    render(ctx: CanvasRenderingContext2D) {
        ctx.fillStyle = '#f00';

        ctx.save();
        ctx.translate(this.vec[0] , this.vec[1] );
        ctx.rotate(this.rotation *  Math.PI / 180);

        ctx.beginPath();
        // ctx.rect(this.x, this.y, this.width, this.height)
        ctx.rect(- this.width * 0.5, - this.height * 0.5, this.width, this.height)
        ctx.fill();

        ctx.restore();

    }
}

const getSlider = (title: string, min: number, max: number) => {
    // <input type="range" min="1" max="100" value="50" class="slider" id="myRange">
    const div = document.createElement('div');
    div.style.display = 'flex';

    const slider = document.createElement('input');
    slider.type = 'range';
    slider.min = `${min}`;
    slider.max = `${max}`;
    slider.defaultValue = '200';

    const text = document.createElement('div');
    text.innerText = title;
    text.style.color = '#aaa';
    div.appendChild(slider);
    div.appendChild(text);

    return {
        div, slider, text
    };
};

const linear = (t: number) => { return t; }
const easeInElastic = (t: number) => { return (.04 - .04 / t) * Math.sin(25 * t) + 1; }
// https://joshondesign.com/2013/03/01/improvedEasingEquations
const easeOutElastic2 = (t: number) => { 
    var p = 0.3; 
    return Math.pow(2, -10 * t) * Math.sin((t - p / 4) * (2 * Math.PI) / p) + 1; 
}
const interpolationElasticValue = (vec: number, v0: number, v1: number, inter: number = 0.5): number => {
    const t = easeOutElastic2(inter)
    let result = vec; // ;colA.slice();
    result = v0 + ((v1 - v0) * t ) ;
    return result;
}

const interpolationElastic = (vec: number[], v0: number[], v1: number[], inter: number = 0.5): number[] => {
    const t = easeOutElastic2(inter)
    let result = [vec[0], vec[1], vec[2]]; // ;colA.slice();
    for (let i = 0; i < 3; i++) {
        result[i] = v0[i] + ((v1[i] - v0[i]) * t ) ;
    }
    // console.log(result);
    return result;
}
const interpolation = (v0: number[], v1: number[], inter: number = 0.5): number[] => {
    let result = [v0[0], v0[1], v0[2]]; // ;colA.slice();
    for (let i = 0; i < 3; i++) {
        result[i] = (result[i] + inter * (v1[i] - v0[i]));
    }
    // console.log(result);
    return result;
}